<template>
  <div class="survey-rating-preview">
    <h2 class="sub-heading">/</h2>
    <div class="preview-main" v-for="(item, i) in summaryData" :key="i" :id="item.id">
      <h3 class="sub-heading">{{item.h3Text}}</h3>
      <InputComponent :form-data="item.list" :is-input="false"></InputComponent>
<!--      <FileUploadMore v-for="item1 in item.list" v-if="item1.valueType == 'picture-card'" :key="item1.title" :title="item1.title" :isPreview="true" :list-type="item1.valueType" :file-list="item1.value" :preview-url="item1.value"></FileUploadMore>-->
      <div class="preview-item stonPar" v-for="(item1,index) in item.list" :key="index">
        <strong class="ston" v-if="index === 20" ></strong>
        <div class="stonDiv" v-if="index === 20">
          <img class="elimage" v-for="(file,index1) in legalPersonObj" @click="privueImg(file.url)" :key="index1" title="" :src="file.url"/>
        </div>
        <strong class="ston" v-if="index === 21" ></strong>
        <div v-if="index === 21">
          <img class="elimage"  v-if="index === 21" v-for="(file,index2) in businessObj" @click="privueImg(file.url)" :key="index2" title="" :src="file.url"/>
        </div>
      </div>
    </div>
    <div class="fast-anchor" id="myScrollspy">
      <div class="anchor-scroll">
        <div class="anchor-cnt">
          <ul class="anchor-list">
            <li v-for="(item,i) in summaryData" :key='i' class="anchor-item" v-bind:class="{active:i == current}">
              <a href="javascript:;" class="title-link" @click="this_scroll(i, item.id)"><i class="title-icon"></i>{{item.h3Text}}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <ControlButton type="ConfirmSubmit" :is-disabled="false" :is-loading="isLoading" :hide-submit="hideSubmit" :save-submit="SaveSubmit"></ControlButton>
    <el-dialog id="imgRate"
      :visible.sync="dialogVisible"
      :show-close="false"
      :close-on-click-modal="true"
      width="700px" :height="height">
      <img  :src="imgSrc" alt="" width="700px" :height="height">
    </el-dialog>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import FileUploadMore from '@/components/FileUpload/FileUploadMore'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  name: 'SurveyRatingPreview',
  components: {
    InputComponent,
    FileUploadMore,
    ControlButton
  },
  data () {
    return {
      imgInfo: {}, // 
      imgSrc: '',
      height: '',
      dialogVisible: false,
      businessObj: [],
      legalPersonObj: [],
      getSurveyRatingUrl: this.$store.state.complianceList.getSurveyRatingUrl,
      saveSurveyRatingUrl: this.$store.state.complianceList.saveSurveyRatingUrl,
      surveyRatingData: {},
      hideSubmit: JSON.parse(localStorage.getItem('hideSubmit')),
      ecc_csrf_token: this.$store.state.complianceList.ecc_csrf_token,
      current: 0,
      isLoading: false
    }
  },
  computed: {
    recordId () {
      return localStorage.getItem('previewRecordId')
    },
    summaryData () {
      let teamData = this.surveyRatingData.checkAgentDutyTeam ? JSON.parse(this.surveyRatingData.checkAgentDutyTeam) : ''
      // console.log(teamData)
      let teamList = []
      console.log('teamData:', teamData)
      if (teamData) {
        for (let i in teamData) {
          teamList = teamList.concat([{
            title: '',
            value: teamData[i].name,
            valueType: 'text'
          }, {
            title: '',
            value: teamData[i].department,
            valueType: 'text'
          }, {
            title: ' ',
            value: teamData[i].responsibility,
            valueType: 'text'
          }, {
            title: ' ',
            value: teamData[i].post ? teamData[i].post : '',
            url: teamData[i].post ? teamData[i].postUrl : '',
            valueType: 'filesteam'
          }])
        }
      }
      // console.log(teamData, teamList)
      return [{
        h3Text: '',
        id: 'OperatingConditions',
        list: [{
          title: '',
          value: this.surveyRatingData.contactName,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.contactTel,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.worksiteAddress,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.legalWorkYear,
          valueType: 'text'
        }, {
          title: '()',
          value: this.surveyRatingData.moneyCapital,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.legalEducation,
          valText: ['', '', '', '', ''],
          valueType: 'select'
        }, {
          title: '',
          value: this.getGraduationDateForm(),
          // value: this.surveyRatingData.legalGraduationTime,
          valueType: 'date'
        }, {
          title: '',
          value: this.surveyRatingData.legalCollege,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.legalMajor,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.legalBadCreditNumber,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.managerNumber,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.stockholderName,
          valueType: 'text'
        }, {
          title: '(%)',
          value: this.surveyRatingData.stockholderRatio,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.companyName,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.companyType,
          valText: ['', '', '', '', '', '', ''],
          valueType: 'select'
        }, {
          title: '',
          value: this.surveyRatingData.companyCode,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.businessLicensePlace,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.legalName,
          valueType: 'text'
        }, {
          title: '()',
          value: this.surveyRatingData.registeredCapital,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.legalCertificateCode,
          valueType: 'text'
        }, {
          title: '',
          name: 'legalImage',
          value: this.surveyRatingData.legalImageUrl,
          valueType: 'picture-card'
        }, {
          title: '',
          name: 'businessLicenseImage',
          value: this.surveyRatingData.businessLicenseImageUrl,
          valueType: 'picture-card'
        }]
      }, {
        h3Text: '',
        id: 'ManagementTeam',
        list: teamList
      }, {
        h3Text: '',
        id: 'FinancialStatus',
        list: [{
          title: '',
          value: this.surveyRatingData.isRegisteredCapitalCoin,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isStatementAudit,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.financialStatementObj ? this.surveyRatingData.financialStatementObj : '',
          url: this.surveyRatingData.financialStatementObj ? this.surveyRatingData.financialStatementUrl : '',
          valueType: 'files'
        }, {
          title: '',
          value: this.surveyRatingData.evidenceObj ? this.surveyRatingData.evidenceObj : '',
          url: this.surveyRatingData.evidenceObj ? this.surveyRatingData.lastYearAuditUrl : '',
          valueType: 'files'
        }, {
          title: '(%)',
          value: this.surveyRatingData.lastYearCashRatio,
          valueType: 'number'
        }, {
          title: '(%)',
          value: this.surveyRatingData.lastYearDebtRatio,
          valueType: 'number'
        }, {
          title: '(%)',
          value: this.surveyRatingData.lastYearCapitalRatio,
          valueType: 'number'
        }, {
          title: '(%)',
          value: this.surveyRatingData.lastYearAccountRatio,
          valueType: 'number'
        }, {
          title: '(%)',
          value: this.surveyRatingData.lastYearGainRatio,
          valueType: 'number'
        }, {
          title: '()',
          value: this.surveyRatingData.lastYearIncoming,
          valueType: 'number'
        }, {
          title: '()',
          value: this.surveyRatingData.lastYearProfit,
          valueType: 'number'
        }, {
          title: '(%)',
          value: this.surveyRatingData.capitalDebtRatio,
          valueType: 'number'
        }, {
          title: '()',
          value: this.surveyRatingData.totalCapital,
          valueType: 'number'
        }, {
          title: '()',
          value: this.surveyRatingData.totalTax,
          valueType: 'number'
        }]
      }, {
        h3Text: '',
        id: 'InternalControl',
        list: [{
          title: '',
          value: this.surveyRatingData.isOrganizationSound,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isInternalControlSound,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isAntiMoneyLaundering,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isRiskManagementSound,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isSecuritySound,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isEmergencySound,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isSupportSound,
          valueType: 'radio'
        }]
      }, {
        h3Text: '',
        id: 'CreditStatus',
        list: [{
          title: '',
          // eslint-disable-next-line eqeqeq
          value: this.surveyRatingData.isMajorLitigation == '1' ? '' : '',
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.litigationNumber,
          valueType: 'number'
        }, {
          title: '()',
          value: this.surveyRatingData.litigationMoney,
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.undoneLitigationNumber,
          valueType: 'number'
        }, {
          title: '()',
          value: this.surveyRatingData.undoneLitigationMoney,
          valueType: 'number'
        }]
      }, {
        h3Text: '',
        id: 'ProfessionalCompetence',
        list: [{
          title: '',
          value: this.getRatingDateForm(),
          // value: this.surveyRatingData.contractStartTime,
          valueType: 'date'
        }, {
          title: '',
          value: this.surveyRatingData.cooperationScope,
          valText: ['', '', '', '', '', '', '', '', '', '', '', this.surveyRatingData.cooperationScope=='11'?this.surveyRatingData.otherBusiness:''],
          valueType: 'select'
        }, {
          title: '',
          value: this.surveyRatingData.cooperationPlace,
          valueType: 'text'
        }, {
          title: '',
          value: this.surveyRatingData.serviceStaffNumber,
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.merchantNumber,
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.machineNumber,
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.companyWorkYear,
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.seniorManagerNumber,
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.institutionNumber,
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.institutionName,
          valueType: 'text'
        }]
      }, {
        h3Text: '',
        id: 'BusinessCompliance',
        list: [{
          title: '',
          value: this.surveyRatingData.conformanceStaffNumber,
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.isConformanceDepartment,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isConformanceRegulation,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isConformanceProcess,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isConformanceText,
          valueType: this.surveyRatingData.isConformanceText==''?'text':'radio'
        }]
      }, {
        h3Text: '',
        id: 'PersonalInfoProtection',
        list: [{
          title: '',
          value: this.surveyRatingData.isSecurityAttestation,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isInformationManagement,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isInformationProcess,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isStaffMaintainSecrecy,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isHierachicalAuthorize,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isInfomationEncryption,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isSecrecyAudit,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isSecrecyDangerCheck,
          valueType: 'radio'
        }]
      }, {
        h3Text: '',
        id: 'RiskControlAbility',
        list: [{
          title: '',
          value: this.surveyRatingData.isPlanElmergencyDrill,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isLaunchElmergencyDrill,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isEmergencyHandle,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isCheckMch,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isTrainMch,
          valueType: 'radio'
        }, {
          title: '',
          value: this.surveyRatingData.isRiskHappened,
          valueType: 'radio'
        }]
      }, {
        h3Text: '',
        id: 'OtherComments',
        list: [{
          title: '',
          value: this.surveyRatingData.agentSuggestion,
          valueType: 'textarea'
        }]
      }]
    }
  },
  created () {
    let obj = JSON.parse(localStorage.getItem('surveyRatingData')) || {}
    if (obj.recordId !== this.recordId) {
      let params = { recordId: this.recordId }
      this.$axios.get(this.getSurveyRatingUrl, { params: params }).then(res => {
        console.log('', res)
        if (res && res.data && res.data.return_code === '0') {
          let list = res.data.list
          if (list.contractStartTime !== '' && list.contractStartTime !== undefined) {
            this.summaryData[0].list[6].value = list.contractStartTime.split(' ', 1)[0]
          }
          if (list.legalGraduationTime !== '' && list.legalGraduationTime !== undefined) {
            this.summaryData[5].list[0].value = list.legalGraduationTime.split(' ', 1)[0]
          }
          list.recordId = this.recordId
          list.statusId = localStorage.getItem('statusId')
          if (list.financialStatementUrl !== '') {
            list.financialStatementObj = JSON.parse(list.financialStatementUrl)
          }
          if (list.lastYearAuditUrl !== '' && list.lastYearAuditUrl !== '[]') {
            list.evidenceObj = JSON.parse(list.lastYearAuditUrl)
          }
          this.surveyRatingData = list
          list.legalPersonObj = this.legalPersonObj = this.surveyRatingData.legalImageUrl !== '' ? JSON.parse(this.surveyRatingData.legalImageUrl) : ''
          list.businessObj = this.businessObj = this.surveyRatingData.businessLicenseImageUrl !== '' ? JSON.parse(this.surveyRatingData.businessLicenseImageUrl) : ''
          if (this.summaryData[8].list[5].value === '1') {
            this.summaryData[8].list = this.summaryData[8].list.concat([{
              title: '',
              value: this.surveyRatingData.riskHappenedNumber,
              valueType: 'number'
            }, {
              title: '',
              value: this.surveyRatingData.isMchRiskHappened,
              valueType: 'radio'
            }, {
              title: '()',
              value: this.surveyRatingData.swindleMoney,
              valueType: 'number'
            }, {
              title: '(%)',
              value: this.surveyRatingData.swindleRatio,
              valueType: 'number'
            }])
          }
          localStorage.setItem('surveyRatingData', JSON.stringify(list))
        } else {
          this.$message.closeAll()
          this.$message({
            // message: res.data.return_msg,
            message: '',
            type: 'error',
            // duration: 0,
            center: true
          })
        }
      }).catch(res => {
        // console.log('', res)
        this.$message.closeAll()
        this.$message({
          message: ',',
          type: 'error',
          // duration: 0,
          center: true
        })
      }) // .finally(() => { this.tableLoading = false })
    } else {
      this.surveyRatingData = obj
      this.legalPersonObj = this.surveyRatingData.legalPersonObj
      this.businessObj = this.surveyRatingData.businessObj
      if (this.summaryData[8].list[5].value === '1') {
        this.summaryData[8].list = this.summaryData[8].list.concat([{
          title: '',
          value: this.surveyRatingData.riskHappenedNumber,
          valueType: 'number'
        }, {
          title: '',
          value: this.surveyRatingData.isMchRiskHappened,
          valueType: 'radio'
        }, {
          title: '()',
          value: this.surveyRatingData.swindleMoney,
          valueType: 'number'
        }, {
          title: '(%)',
          value: this.surveyRatingData.swindleRatio,
          valueType: 'number'
        }])
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    privueImg (url) {
      this.imgSrc = ''
      this.height = 700
      var img = new Image()
      img.src = url
      const vm = this
      img.onload = function () {
        var heighty = img.height ? img.height : 600
        var width = img.width ? img.width : 600
        console.log('heighty:', heighty)
        console.log('width:', width)
        vm.height = 700 * (heighty / width)
        vm.imgSrc = url
      }
      this.dialogVisible = true
    },
    getGraduationDateForm () {
      let times = this.surveyRatingData.legalGraduationTime
      console.log('legalGraduationTime:', times)
      if (times !== '' && times !== undefined) {
        times = times.split(' ', 1)[0]
      }
      return times
    },
    getRatingDateForm () {
      let times = this.surveyRatingData.contractStartTime
      console.log('contractStartTime:', times)
      if (times !== '' && times !== undefined) {
        times = times.split(' ', 1)[0]
      }
      return times
    },
    this_scroll (i, id) {
      this.current = i
      document.getElementById(id).scrollIntoView()
    },
    handleScroll () {
      let scrolled = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      if (scrolled > 0 && scrolled < document.getElementById('ManagementTeam').offsetTop) {
        this.current = 0
      } else if (scrolled >= document.getElementById('ManagementTeam').offsetTop && scrolled < document.getElementById('FinancialStatus').offsetTop) {
        this.current = 1
      } else if (scrolled >= document.getElementById('FinancialStatus').offsetTop && scrolled < document.getElementById('InternalControl').offsetTop) {
        this.current = 2
      } else if (scrolled >= document.getElementById('InternalControl').offsetTop && scrolled < document.getElementById('CreditStatus').offsetTop) {
        this.current = 3
      } else if (scrolled >= document.getElementById('CreditStatus').offsetTop && scrolled < document.getElementById('ProfessionalCompetence').offsetTop) {
        this.current = 4
      } else if (scrolled >= document.getElementById('ProfessionalCompetence').offsetTop && scrolled < document.getElementById('BusinessCompliance').offsetTop) {
        this.current = 5
      } else if (scrolled >= document.getElementById('BusinessCompliance').offsetTop && scrolled < document.getElementById('PersonalInfoProtection').offsetTop) {
        this.current = 6
      } else if (scrolled >= document.getElementById('PersonalInfoProtection').offsetTop && scrolled < document.getElementById('RiskControlAbility').offsetTop) {
        this.current = 7
      } else if (scrolled >= document.getElementById('RiskControlAbility').offsetTop && scrolled < document.getElementById('OtherComments').offsetTop) {
        this.current = 8
      } else if (scrolled >= document.getElementById('OtherComments').offsetTop && scrolled < document.getElementById('myScrollspy').offsetTop) {
        this.current = 9
      } else if (scrolled >= document.getElementById('myScrollspy').offsetTop) {
        this.current = 10
      }
    },
    SaveSubmit () {
       if(this.surveyRatingData.companyType=='0'){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.contactName==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.contactTel==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.legalImage==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.businessLicenseImage==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.companyName==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.companyCode==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.businessLicensePlace==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.legalName==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.legalCertificateCode==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.financialStatement==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.isMajorLitigation=='1' && this.surveyRatingData.litigationNumber==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.isMajorLitigation=='1' && this.surveyRatingData.litigationMoney==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.isMajorLitigation=='1' && this.surveyRatingData.undoneLitigationNumber==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.isMajorLitigation=='1' && this.surveyRatingData.undoneLitigationMoney==''){
        this.$message.error('');
        return;
      }

      if(this.surveyRatingData.contractStartTime==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.cooperationScope==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.cooperationPlace==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.otherBusiness=='' && this.surveyRatingData.cooperationScope=='11'){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.serviceStaffNumber==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.merchantNumber==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.machineNumber==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.companyWorkYear==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.seniorManagerNumber==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.conformanceStaffNumber==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.isRiskHappened=='1' && this.surveyRatingData.riskHappenedNumber==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.isRiskHappened=='1' && this.surveyRatingData.swindleMoney==''){
        this.$message.error('');
        return;
      }
      if(this.surveyRatingData.isRiskHappened=='1' && this.surveyRatingData.swindleRatio==''){
        this.$message.error('');
        return;
      }

      this.isLoading = true
      let params = new FormData() // form
      for (let i in this.surveyRatingData) {
        params.append(i, this.surveyRatingData[i])
      }
      params.append('ecc_csrf_token', this.ecc_csrf_token)
      // console.log(this.surveyRatingData, params)
      let config = { headers: { 'Content-Type': 'multipart/form-data' } }
      this.$axios.post(this.saveSurveyRatingUrl, params, config).then(res => {
        console.log('', res)
        this.isLoading = false
        if (res && res.data && res.data.return_code === '0') {
          sessionStorage.setItem('submitStatus', true)
          this.$router.push('ResultSubmitted')
        } else {
          console.log(':',res)
          sessionStorage.setItem('submitStatus', false)
          this.$message.closeAll()
          // console.log('res:',this.ecc_csrf_token)
          this.$message({
            // message:res.return_msg,
            message: '',
            // message:this.ecc_csrf_token?'':'',
            type: 'error',
            // duration: 0,
            center: true
          })
        }
      }).catch(res => {
        console.log('', res)
        this.isLoading = false
        sessionStorage.setItem('submitStatus', false)
        this.$message.closeAll()
        this.$message({
          message: ',',
          type: 'error',
          // duration: 0,
          center: true
        })
      }) // .finally(() => { this.tableLoading = false })
      //
      var WXPAY_JSLOG_REPORT = null;
      if(WXPAY_JSLOG_REPORT) WXPAY_JSLOG_REPORT.send({common_log:"SurveyRatingSubmit"}); // 
      
    }
  }
}
</script>

<style>
#imgRate .el-dialog__body,#imgRate .el-dialog__header{
  padding: 0 !important;
}
.survey-rating-preview .preview-main {
  background-color: #fff;
  /* min-height: 880px; */
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  margin-bottom: 20px;
  box-shadow: #ddd 0 0 6px;
  display: flex;
  flex-wrap: wrap;
}
.survey-rating-preview .preview-main:nth-last-child(3) {
  margin-bottom: 140px;
}
.survey-rating-preview .preview-main .input-component {
  width: 100%;
  border-bottom: 1px solid #ddd;
  /* border-bottom: 1px solid #ddd; */
}
.survey-rating-preview .preview-main:nth-last-child(3) .input-component {
  border: none;
}
.survey-rating-preview .preview-main .file-upload {
  width: 50%;
  padding: 20px 0;
  border-bottom: 1px solid #ddd;
}
.survey-rating-preview .control-button {
  width: 100%;
  height: 90px;
  background: #fff;
  box-shadow: 0 0 6px #ddd;
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
#myScrollspy {
  /* width: 240px; */
  position: fixed;
  top: 130px;
  right: 0%;
  /* margin-left:510px; */
}
#myScrollspy .anchor-item:after {
  content: '';
  display: block;
  width: 1px;
  height: 30px;
  background: #ddd;
  margin-left: 9px;
}
#myScrollspy .anchor-item:last-child:after {
  content: none;
}
#myScrollspy .anchor-item a.title-link {
  color: #2c3e50;
  font-weight: bold;
  text-decoration: none;
  outline: none !important;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 3px 0;
}
#myScrollspy .anchor-item a.title-link i.title-icon {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: rgba(0, 194, 80, 0);
  margin-right: 20px;
}
#myScrollspy .anchor-item a.title-link i.title-icon:before {
  content: '';
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #ddd;
}
#myScrollspy .anchor-item.active a.title-link i.title-icon {
  background: rgba(0, 194, 80, 0.3) !important;
}
#myScrollspy .anchor-item.active a.title-link i.title-icon:before {
  background: limegreen;
}
</style>

<style>
.survey-rating-preview .preview-main .input-component .input-component-item {
  width: 50%;
  padding: 20px 0;
  border-top: 1px solid #ddd;
  flex: auto;
}
.survey-rating-preview .preview-main .input-component-item strong,
.survey-rating-preview .preview-main .file-upload strong {
  width: 93px;
}
.survey-rating-preview .preview-main .file-upload .img-preview {
  width: 64px;
  height: 64px;
}
.survey-rating-preview .preview-main:nth-last-child(3) .input-component-item {
  align-items: flex-start;
  border: none;
  flex: none;
}
.survey-rating-preview
  .preview-main:nth-last-child(3)
  .input-component-item
  span {
  color: #606266;
  line-height: 1.5;
}
.elimage{
  width: 100px;
  height: 100px;
  margin-top: 30px;
  margin-left: 30px;
  margin-right: 10px;
}
.ston{
  float: left;
  font-weight: 600;
  line-height: 146px;
}
.stonPar{
  width: 50%;
}
.stonDiv{
    float: left;
}
/* .input-component .input-component-item span{
  padding-right:25px;
} */
.input-component div:nth-child(17) span{
  padding-right:20px;
}

</style>



