<template>
  <div class="professional-competence">
    <h2 class="sub-heading"></h2>
    <div class="professional-competence-main">
      <!-- <InputComponent :form-data="professionalData"></InputComponent> -->
      <el-form :model="professionalForm" :rules="formRules" ref="professionalForm" label-width="100px" label-position="left" class="professional-form">
        <el-form-item v-for="(item, i) in professionalData" :label="item.title" :prop="item.key" :key="i">
          <el-select v-if="item.valueType == 'select'" v-model="professionalForm[item.key]">
            <el-option v-for="(itemj, j) in item.options" :label="itemj.text" :value="itemj.value" :key="j + itemj.text"></el-option>
          </el-select>
          <el-date-picker v-else-if="item.valueType == 'date'" type="date" placeholder="" v-model="professionalForm[item.key]" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
          <el-date-picker v-else-if="item.valueType == 'daterange'" type="daterange" start-placeholder="" end-placeholder="" v-model="professionalForm[item.key]" range-separator="-" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
          <el-input v-else-if="item.valueType == 'number'" type="number" v-model="professionalForm[item.key]"></el-input>
          <el-input v-else v-model="professionalForm[item.key]"></el-input>
          <el-input v-if="item.valueType == 'select' &&  otherHandle" type="text"  v-model="otherBusiness" @blur="otherTextValue" placeholder=""></el-input>
         
        </el-form-item>
      </el-form>
      <ControlButton :next-path="nextPath" :is-disabled="!professionalCompetenceStatus" :save="Save"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import ControlButton from '@/components/ControlButton/ControlButton'
import { isPositiveInteger } from '@/units/validator'

export default {
  name: 'ProfessionalCompetence',
  components: {
    InputComponent,
    ControlButton
  },
  data () {
    return {
      surveyRatingData: JSON.parse(localStorage.getItem('surveyRatingData')),
      keyStr: {
        contractStartTime: '',
        cooperationScope: '',
        cooperationPlace: '',
        serviceStaffNumber: '',
        merchantNumber: '',
        machineNumber: '',
        companyWorkYear: '',
        seniorManagerNumber: '',
        institutionNumber: '',
        institutionName: '',
      },
      professionalData: [],
      professionalForm: {},
      //
      otherHandle:false,
      //
       otherBusiness:'',

      formRules: {
        contractStartTime: [
          { required: true, message: '', trigger: 'blur' },
          { required: true, message: '', trigger: 'change' }
        ],
        cooperationScope: [
          { required: true, message: '', trigger: 'blur' },
          { required: true, message: '', trigger: 'change' }
        ],
        cooperationPlace: [
          { required: true, message: '', trigger: 'blur' }
        ],
        serviceStaffNumber: [
          { required: true, message: '', trigger: 'blur' },
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        merchantNumber: [
          { required: true, message: '', trigger: 'blur' },
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        machineNumber: [
          { required: true, message: '', trigger: 'blur' },
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        companyWorkYear: [
          { required: true, message: '', trigger: 'blur' },
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        seniorManagerNumber: [
          { required: true, message: '', trigger: 'blur' },
          { validator: isPositiveInteger, trigger: 'blur' }
        ],
        institutionNumber: [
          { validator: isPositiveInteger, trigger: 'blur' }
        ]
           
      },
      nextPath: 'BusinessCompliance'
    }
  },
  computed: {
    professionalCompetenceStatus () {
      let status = true
      if(this.otherBusiness=='' && this.surveyRatingData.cooperationScope=='11'){
        status = false;
      }
      // let status = true
      for (let i in this.keyStr) {
        if (!this.surveyRatingData[i] && i !== 'institutionNumber' && i !== 'institutionName') status = false
      }
      return status
    }
  },
  
  created () {
    // console.log(this.surveyRatingData)
    for (let i in this.keyStr) {
        let obj = { title: this.keyStr[i], key: i, value: this.surveyRatingData[i], isRiequired: true, valueType: 'Number' }
      if (i === 'contractStartTime') {
        if (obj.value === '1970-01-01 00:00:01') { obj.value = '' } else { obj.value = obj.value.slice(0, 10) }
        obj.valueType = 'date'
      } else if (i === 'cooperationScope') {
        obj.valText = ['', '', '', '', '', '', '', '', '', '', '', '']
        obj.value === '0' && (obj.value = '')
        obj.options = [{ text: '', value: '1' }, { text: '', value: '2' }, { text: '', value: '3' }, { text: '', value: '4' }, { text: '', value: '5' }, { text: '', value: '6' }, { text: '', value: '7' }, { text: '', value: '8' }, { text: '', value: '9' }, { text: '', value: '10' }, { text: '', value: '11' }]
        obj.valueType = 'select'
        
       
    
      } else if (i === 'cooperationPlace' || i === 'institutionName') {
        obj.valueType = 'text'
      }
     
      if (i === 'institutionNumber' || i === 'institutionName') {
        obj.isRiequired = false
      }
      this.professionalData.push(obj)
      }
    let professionalForm = {}
   
    for (let i in this.professionalData) {
      professionalForm[this.professionalData[i].key] = this.professionalData[i].value
    }
    this.professionalForm = professionalForm

    // this.surveyRatingData.institutionNumber=='0'?'':this.surveyRatingData.institutionNumber
    //value
    this.otherBusiness = this.surveyRatingData.otherBusiness || '';
   
    // console.log('professionalData:',this.professionalData, 'professionalForm:',this.professionalForm)
  },
  watch: {
    professionalForm: {
      handler (val, oldval) {
        for (let i in val) {
          if (i === 'cooperationScope') {
            // console.log(i, val[i], this.surveyRatingData.cooperationScope)
            //
            if(val[i]=='11'){
              this.otherHandle = true;
            }else{
              this.otherHandle = false;
            }
          }
          if (i === 'contractStartTime') {
            this.surveyRatingData.contractStartTime = val[i] ? val[i] + ' 00:00:01' : ''
          } else {
            this.surveyRatingData[i] = val[i]
          }
        }
      },
      immediate: false,
      deep: true
    }
  },
  methods: {
    //
    otherTextValue(){
      this.$set(this.surveyRatingData,'otherBusiness',this.otherBusiness)
     
    },

    Save () {
      
      let formStatus = true
      // this.$refs['professionalForm'].validate((valid) => {
      //   if (!valid) {
      //     formStatus = false
      //     return false
      //   }
      // })
      if (formStatus) {
        localStorage.setItem('surveyRatingData', JSON.stringify(this.surveyRatingData))
        this.$message.closeAll()
        this.$message({
          message: '',
          type: 'success',
          // duration: 0,
          center: true
        })
        return true
      }
      return false
    }
  }
}
</script>

<style scoped>
.professional-competence .professional-competence-main {
  background-color: #fff;
  min-height: 600px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.professional-competence .professional-competence-main .professional-form {
  padding-top: 30px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
  justify-content: space-between;
}
</style>

<style>
.professional-competence-main .professional-form .professional-form-item {
  min-height: 74px;
}
.professional-competence-main .professional-form-item strong {
  width: 93px;
}
</style>



