import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/pages/HelloWorld'
// 
import ComplianceHomepage from '@/pages/ComplianceHomepage/ComplianceHomepage'
// -
import InspectionList from '@/pages/InspectionList/InspectionList'
// -
import QualificationInfo from '@/pages/QualificationInfo/QualificationInfo'
// -
import EnterpriseInfo from '@/pages/EnterpriseInfo/EnterpriseInfo'
// -
import InspectionPhotos from '@/pages/InspectionPhotos/InspectionPhotos'
// -
import InspectionListPreview from '@/pages/InspectionListPreview/InspectionListPreview'

// -
import SurveyRating from '@/pages/SurveyRating/SurveyRating'
// -
import OperatingConditions from '@/pages/OperatingConditions/OperatingConditions'
// -
import ManagementTeam from '@/pages/ManagementTeam/ManagementTeam'
// -
import FinancialStatus from '@/pages/FinancialStatus/FinancialStatus'
// -
import InternalControl from '@/pages/InternalControl/InternalControl'
// -
import CreditStatus from '@/pages/CreditStatus/CreditStatus'
// -
import ProfessionalCompetence from '@/pages/ProfessionalCompetence/ProfessionalCompetence'
// -
import BusinessCompliance from '@/pages/BusinessCompliance/BusinessCompliance'
// -
import PersonalInfoProtection from '@/pages/PersonalInfoProtection/PersonalInfoProtection'
// -
import RiskControlAbility from '@/pages/RiskControlAbility/RiskControlAbility'
// -
import OtherComments from '@/pages/OtherComments/OtherComments'
// -
import SurveyRatingPreview from '@/pages/SurveyRatingPreview/SurveyRatingPreview'
// -
import ResultSubmitted from '@/pages/ResultSubmitted/ResultSubmitted'
// -
import ResultAudit from '@/pages/ResultAudit/ResultAudit'

//test
// import Test from '@/pages/Test/Test'

Vue.use(Router)



export default new Router({
  mode: 'history',
  base: '/index.php/xphp/ccagentcheck/',
  // base: '/index.php/xphp/ccviolated_mch_handle_v2/',
  routes: [
    // {
    //   path: '/HelloWorld',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    {
      path: '/serviceProvideCheck',
      // path:'/',
      // path: '/ComplianceHomepage',
      name: 'ComplianceHomepage',
      component: ComplianceHomepage
    },
    {
      path: '/InspectionList',
      name: 'InspectionList',
      component: InspectionList
    },
    {
      path: '/QualificationInfo',
      name: 'QualificationInfo',
      component: QualificationInfo
    },
    {
      path: '/EnterpriseInfo',
      name: 'EnterpriseInfo',
      component: EnterpriseInfo
    },
    {
      path: '/InspectionPhotos',
      name: 'InspectionPhotos',
      component: InspectionPhotos
    },
    {
      path: '/InspectionListPreview',
      name: 'InspectionListPreview',
      component: InspectionListPreview
    },
    {
      path: '/SurveyRating',
      name: 'SurveyRating',
      component: SurveyRating
    },
    {
      path: '/OperatingConditions',
      name: 'OperatingConditions',
      component: OperatingConditions
    },
    {
      path: '/ManagementTeam',
      name: 'ManagementTeam',
      component: ManagementTeam
    },
    {
      path: '/FinancialStatus',
      name: 'FinancialStatus',
      component: FinancialStatus
    },
    {
      path: '/InternalControl',
      name: 'InternalControl',
      component: InternalControl
    },
    {
      path: '/CreditStatus',
      name: 'CreditStatus',
      component: CreditStatus
    },
    {
      path: '/ProfessionalCompetence',
      name: 'ProfessionalCompetence',
      component: ProfessionalCompetence
    },
    {
      path: '/BusinessCompliance',
      name: 'BusinessCompliance',
      component: BusinessCompliance
    },
    {
      path: '/PersonalInfoProtection',
      name: 'PersonalInfoProtection',
      component: PersonalInfoProtection
    },
    {
      path: '/RiskControlAbility',
      name: 'RiskControlAbility',
      component: RiskControlAbility
    },
    {
      path: '/OtherComments',
      name: 'OtherComments',
      component: OtherComments
    },
    {
      path: '/SurveyRatingPreview',
      name: 'SurveyRatingPreview',
      component: SurveyRatingPreview
    },
    {
      path: '/ResultSubmitted',
      name: 'ResultSubmitted',
      component: ResultSubmitted
    },
    {
      path: '/ResultAudit',
      name: 'ResultAudit',
      component: ResultAudit
    }
  ]
})



