/*  2 */
export function validatetoFixedTwoNew(value, number, msg, callback) {
  // const reg = /^(([1-9]{1}\d*)|([0]{1}))(\.(\d){1,2})?$/
  const reg = /^(([1-9]{1}\d*)|(0{1}))(\.\d{1,2})?$/


  // console.log(111, value,value.length)
  if (value === '') {
    callback()
  } else {
    if (value.indexOf('.') != -1 && value.length > 17) {
      callback(new Error('16'))
    } else if (value.indexOf('.') == -1 && value.length > 16) {
      callback(new Error('16'))
    }
    // console.log('test:',reg.test(value))
    if (!reg.test(value)) {
      // console.log(666)
      callback(new Error(''))
    } else {
      // var num = value.split('.').length - 1
      console.log('length:', value.split('.').length)
      if (value.split('.').length > 2) {
        callback(new Error(''))
      } else {
        callback()
      }
      callback()
    }
  }
  if (value.length > number) {
    callback(new Error(msg))
  } else {
    callback()
  }
}

//  0
export function isPositiveInteger(rule, value, callback) {
  const reg = /^[+]{0,1}(\d+)$/
  if (value === '' || value === undefined || value == null) {
    callback()
  } else {
    if (!reg.test(value)) {
      callback(new Error(''))
    } else {
      callback()
    }
  }
}

//  [0-100]
export function isBtnZeroToHundred(rule, value, callback) {
  const reg = /^(\d|[1-9]\d|100)(\.\d{1,2})?$/

  // console.log(value, ':', value.length)
  if (value === '' || value === undefined || value == null) {
    callback()
  } else {
    // console.log(reg.test(value))
    if (!reg.test(value)) {
      callback(new Error('[0,100],'))
    } else if (value > 100) {
      callback(new Error('[0,100],'))
    } else {
      var num = value.split('.').length - 1
      if (num > 1) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
  }
}

//[-100-100]
export function isBtnZeroToHundredTwo(rule, value, callback) {
  const reg = /^(\d|[1-9]\d|100)(\.\d{1,2})?$/
  const reg2 = /^\-(100|[1-9]\d|[1-9])(\.\d{1,2})?$/

  // console.log(':', value, '', value.length)

  if (value === '' || value === undefined || value == null) {
    callback()
  } else {
    // console.log('reg:', reg.test(value), 'reg2:', reg2.test(value))
    if (!reg.test(value) && !reg2.test(value)) {
      // console.log('')
      callback(new Error('[-100,100],'))
    } else if (value > 100 || value < -100) {
      // console.log('')
      callback(new Error('[-100,100],'))
    } else {
      var num = value.split('.').length - 1

      if (num > 1) {
        callback(new Error(''))
      } else {
        callback()
      }
    }

  }
}


