<template>
  <div class="enterprise-info">
    <h2 class="sub-heading"></h2>
    <div class="enterprise-main">
      <h3 class="sub-heading" v-if="unfillData.length"></h3>
      <el-form :model="unfillForm" :rules="formRules" ref="unfillForm" label-width="100px" label-position="left" class="unfill-form">
        <el-form-item v-for="(item, i) in unfillData" :label="item.title" :prop="item.key" :key="i">
          <el-select v-if="item.valueType == 'select'" v-model="unfillForm[item.key]">
            <el-option v-for="(itemj, j) in item.options" :label="itemj.text" :value="itemj.value" :key="j"></el-option>
          </el-select>
          <el-date-picker v-else-if="item.valueType == 'date'" type="date" placeholder="" v-model="unfillForm[item.key]" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
          <el-date-picker v-else-if="item.valueType === 'daterange'" type="daterange" start-placeholder="" end-placeholder="" v-model="unfillForm[item.key]" range-separator="-" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
          <el-input v-else v-model="unfillForm[item.key]" :placeholder="item.placehoder"></el-input>
        </el-form-item>
      </el-form>
      <h3 class="sub-heading"></h3>
      <div class="input-enable" @click="InputEnable">{{isInput ? "" : ""}}</div>
      <div class="filled">
        <el-form :model="fillForm" :rules="formRules" ref="fillForm" label-width="100px" label-position="left" class="fill-form">
          <el-form-item v-for="(item, i) in fillData" :label="item.title" :prop="item.key" :key="i">
            <p v-if="!isInput">{{valueText(item, fillForm[item.key])}}</p>
            <el-select v-else-if="item.valueType == 'select'" v-model="fillForm[item.key]">
              <el-option v-for="(itemj, j) in item.options" :label="itemj.text" :value="itemj.value" :key="j"></el-option>
            </el-select>
            <el-date-picker v-else-if="item.valueType == 'date'" type="date" placeholder="" v-model="fillForm[item.key]" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <el-date-picker v-else-if="item.valueType === 'daterange'" type="daterange" start-placeholder="" end-placeholder="" v-model="fillForm[item.key]" range-separator="-" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            <el-input v-else v-model="fillForm[item.key]"></el-input>
          </el-form-item>
        </el-form>
        <!-- <InputComponent :form-data="fillData" :is-input="isInput"></InputComponent> -->
      </div>
      <ControlButton :next-path="nextPath" :save="Save" :is-disabled="!enterpriseInfoStatus"></ControlButton>
    </div>
  </div>
</template>

<script>
import InputComponent from '@/components/InputComponent/InputComponent'
import ControlButton from '@/components/ControlButton/ControlButton'

export default {
  components: {
    InputComponent,
    ControlButton
  },
  data () {
    return {
      inspectionListData: JSON.parse(localStorage.getItem('inspectionListData')),
      keyStr: {
        // id: 'ID',
        companyName: '',
        companyNature: '',
        agentCertificateType: '',
        agentCertificateCode: '',
        // institutionCertificateImage: '',
        legalName: '',
        legalCertificateType: '',
        legalCertificateCode: '',
        // legalImage: '',
        agentSignTime: '',
        agentCertificateStartTime: '()',
        // agentCertificateEndTime: '()',
        agentCertificatePlace: '',
        companyPlace: '',
        businessScope: ''
        // worksiteImage: ''
      },
      unfillData: [],
      unfillForm: {},
      fillData: [],
      fillForm: {},
      formRules: {
        companyName: [
          { required: true, message: '', trigger: 'blur' }
          // { min: 2, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        companyNature: [
          { required: true, message: '', trigger: 'change' }
        ],
        agentCertificateType: [
          { required: true, message: '', trigger: 'change' }
        ],
        agentCertificateCode: [
          { required: true, message: '', trigger: 'blur' }
        ],
        legalName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        legalCertificateType: [
          { required: true, message: '', trigger: 'change' }
        ],
        legalCertificateCode: [
          { required: true, message: '', trigger: 'blur' }
        ],
        agentSignTime: [
          { required: true, message: '', trigger: 'blur' },
          { required: true, message: '', trigger: 'change' }
        ],
        agentCertificateStartTime: [
          { required: true, message: '', trigger: 'blur' },
          { required: true, message: '', trigger: 'change' }
        ],
        agentCertificatePlace: [
          { required: true, message: '', trigger: 'blur' }
        ],
        companyPlace: [
          { required: true, message: '', trigger: 'blur' }
        ],
        businessScope: [
          { required: false, message: '', trigger: 'blur' }
        ]
      },
      isInput: false,
      initFillForm: '',
      nextPath: 'InspectionPhotos'
    }
  },
  computed: {
    enterpriseInfoStatus () {
      let status = true
      for (let i in this.keyStr) {
        if (!this.inspectionListData[i] && i !== 'businessScope') status = false
      }
      return status
    }
  },
  watch: {
    unfillForm: {
      handler (val, oldval) {
        this.Update(val)
      },
      immediate: false,
      deep: true
    },
    fillForm: {
      handler (val, oldval) {
        this.Update(val)
      },
      immediate: false,
      deep: true
    }
  },
  created () {
    for (let i in this.keyStr) {
      let obj = { title: this.keyStr[i], value: this.inspectionListData[i], key: i, isRiequired: true, valueType: 'text' }
      if (i === 'businessScope') obj.isRiequired = false
      switch (i) {
        case 'companyNature': // 
          obj.valText = ['', '', '', '', '', '', '']
          obj.value === '0' && (obj.value = '')
          obj.options = [{ text: '', value: '1' }, { text: '', value: '2' }, { text: '', value: '3' }, { text: '', value: '4' }, { text: '', value: '5' }, { text: '', value: '6' }]
          obj.valueType = 'select'
          break
        case 'agentCertificateType': // 
          obj.valText = ['', '']
          obj.value === '0' && (obj.value = '')
          obj.options = [{ text: '', value: '1' }]
          obj.valueType = 'select'
          break
        case 'legalCertificateType': // 
          obj.valText = ['', '']
          obj.options = [{ text: '', value: '0' }, { text: '', value: '1' }]
          obj.valueType = 'select'
          break
        case 'agentSignTime': // 
          if (obj.value === '1970-01-01 00:00:01') { obj.value = '' } else { obj.value = obj.value.slice(0, 10) }
          obj.valueType = 'date'
          break
        case 'agentCertificateStartTime': // ()
          obj.title = ''
          if (this.inspectionListData.agentCertificateStartTime === '1970-01-01 00:00:01' || this.inspectionListData.agentCertificateStartTime === '' || this.inspectionListData.agentCertificateEndTime === '1970-01-01 00:00:01' || this.inspectionListData.agentCertificateEndTime === '') {
            obj.value = []
          } else {
            obj.value = [this.inspectionListData.agentCertificateStartTime.slice(0, 10), this.inspectionListData.agentCertificateEndTime.slice(0, 10)]
          }
          obj.valueType = 'daterange'
          break
        case 'agentCertificatePlace': case 'companyPlace': // 
          obj.placehoder = '233'
          break
        default:
          break
      }
      obj.value ? this.fillData.push(obj) : this.unfillData.push(obj)
    }

    let unfillForm = {}
    let fillForm = {}
    for (let i in this.unfillData) {
      unfillForm[this.unfillData[i].key] = this.unfillData[i].value
    }
    for (let i in this.fillData) {
      fillForm[this.fillData[i].key] = this.fillData[i].value
    }
    this.unfillForm = unfillForm
    this.fillForm = fillForm
  },
  methods: {
    valueText (item, value) {
      let text = Array.isArray(value) ? value[0] + ' - ' + value[1] : item.valueType === 'select' ? item.valText[value] : item.valueType === 'radio' ? (value === '1' ? '' : '') : value
      return text
    },
    InputEnable () {
      if (!this.isInput) {
        this.initFillForm = JSON.stringify(this.fillForm)
      } else {
        this.fillForm = JSON.parse(this.initFillForm)
        let _this = this
        setTimeout(() => {
          _this.$refs['fillForm'].validate((valid) => {
            if (!valid) {
              return false
            }
          })
        }, 50)
      }
      this.isInput = !this.isInput
    },
    Save () {
      this.isInput = false
      localStorage.setItem('inspectionListData', JSON.stringify(this.inspectionListData))
      this.$message.closeAll()
      this.$message({
        message: '',
        type: 'success',
        // duration: 0,
        center: true
      })
      // return false
    },
    Update (data) {
      for (let i in data) {
        // console.log(i, data[i])
        if (i === 'agentCertificateStartTime') {
          this.inspectionListData.agentCertificateStartTime = data[i] && data[i][0] ? data[i][0] + ' 00:00:01' : ''
          this.inspectionListData.agentCertificateEndTime = data[i] && data[i][1] ? data[i][1] + ' 00:00:01' : ''
        } else if (i === 'agentSignTime') {
          this.inspectionListData.agentSignTime = data[i] ? data[i] + ' 00:00:01' : ''
        } else {
          this.inspectionListData[i] = data[i]
        }
      }
      // console.log(this.inspectionListData)
    }
  }
}
</script>

<style scoped>
.enterprise-info {
}
.enterprise-info .enterprise-main {
  background-color: #fff;
  min-height: 780px;
  box-sizing: border-box;
  padding: 24px;
  border-radius: 2px;
  box-shadow: #ddd 0 0 6px;
}
.enterprise-info .enterprise-main .input-enable {
  width: max-content;
  color: cornflowerblue;
  cursor: pointer;
  margin-top: -14px;
  display: flex;
  justify-content: flex-end;
  margin-left: auto;
}
.enterprise-info .enterprise-main .unfilled {
  padding: 15px 0 30px 0;
  border-bottom: 1px solid #ddd;
  margin-bottom: 30px;
}
.enterprise-info .enterprise-main .filled {
  padding: 10px 0 30px 0;
  border-bottom: 1px solid #ddd;
  margin-bottom: 40px;
}
.enterprise-info .enterprise-main .input-component {
  justify-content: space-between;
}
</style>



